<script>
	import { groupCollapsed } from "console";
	import Question from './Question.svelte';
	import Answer from './Answer.svelte';
	
	let currRank = 0;
	export let name;
	export let ranks = ["Porcelain", "Obsidian", "Steel", "Sapphire", "Emerald", "Ruby", "Bronze", "Silver", "Gold", "Platinum"];
	export let answerKeys = ['A', 'B', 'C', 'D'];
	let quizIdx = 0;

	let rankTags = document.getElementsByClassName('tag');
	

	const quiz = [
		{
			question: 'This is the question',
			correctAnswer: 0,
			answers: [
				'A',
				'B',
				'C',
				'D'
			],
		},
		{
			question: 'This is question 2',
			correctAnswer: 1,
			answers: [
				'A',
				'B',
				'C',
				'D'
			],
		}
	]

	function checkAnswerHandler(answerText){
		const isCorrect = quiz[quizIdx].answers.indexOf(answerText) === quiz[quizIdx].correctAnswer;
		if (isCorrect) {
			currRank += 1;
			quiz[quizIdx].question = 'Correct!';
			setTimeout(() => {
				quizIdx += 1;
			}, 2000)
		}
		else {
			quiz[quizIdx].question = 'Wrong!';
		}
	}
</script>

<main>
	<div class="board">
		{#each ranks as rank, id}
			<div class="tag-area">
				<div class="bar"></div>
					{#if id === currRank}
						<div class="tag curr">{rank}</div>
					{:else}
					<div class="tag">{rank}</div>
					{/if}
				<div class="bar"></div>
			</div>
		{/each}
	</div>
	<Question questionText={quiz[quizIdx].question}/>
	<div class="answers">
		{#each quiz[quizIdx].answers as answer, idx }
			<div class="answer-container">
				<span class="answer-letter">{answerKeys[idx]}</span>
				<Answer answerText={answer} {checkAnswerHandler} />
			</div>
		{/each}	
	</div>

</main>

